

























import { Vue, Component, Mixins, Prop } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";

import { DatePickerOptions } from "element-ui/types/date-picker";

@Component
export default class MyDatePicker extends Mixins(TwoWay) {
    @Prop({ default: true })
    clearable!: boolean;

    get pickerOptionsStart() {
        let _this = this;
        let pickerOptionsStart: DatePickerOptions = {
            disabledDate(time) {
                let current = new Date(_this.currentValue[1] || "");
                current.setDate(current.getDate() - 2);
                return time.getTime() > current.getTime();
            }
        };
        return pickerOptionsStart;
    }

    get pickerOptionsEnd() {
        let _this = this;
        let pickerOptionsEnd: DatePickerOptions = {
            disabledDate(time) {
                let current = new Date(_this.currentValue[0] || "");
                current.setDate(current.getDate() + 5);
                return time.getTime() < current.getTime();
            }
        };
        return pickerOptionsEnd;
    }
}
